@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* swiper slider */
/* slider pagination */
.swiper-pagination {
  pointer-events: none;
  line-height: 1; }

.swiper-pagination-bullet {
  $size: 1.0rem 3.0rem;
  $size--mob: 0.6rem 1.8rem;
  display: inline-block;
  vertical-align: top;
  height: nth($size, 1);
  width: 10rem;
  max-width: nth($size, 1);
  border-radius: round-float(nth($size, 1) / 2, 1);
  border: 0.2rem solid $highlight-color;
  margin: 1rem 0.5rem 0;
  opacity: 1;
  pointer-events: auto;
  background-color: transparent;
  box-sizing: border-box;
  outline: none;
  // transition
  $prop: opacity, max-width, background-color, border-color;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  // hover
  &:not(.swiper-pagination-bullet-active):hover {
    background-color: $highlight-color--hover;
    border-color: $highlight-color--hover; }

  &.swiper-pagination-bullet-active {
    cursor: default;
    max-width: nth($size, 2);
    background-color: $base-color-alt;
    border-color: $base-color-alt;
    opacity: 0.2; } }

.swiper-container-horizontal {
  & > .swiper-pagination-bullets {
    bottom: 0;

    .swiper-pagination-bullet {
      margin: 1rem 0.5rem 0; } } }

/* swiper slider end */
