@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* form elements */
label {
  display: inline-block;
  vertical-align: top;
  margin-bottom: 0.5rem;
  @include font-size($font-size, $line-height);
  color: $text-color;
  user-select: none;
  -webkit-tap-highlight-color: transparent !important;
  transition: color $animation-speed;

  mark {
    background: none;
    color: $error-color; }

  & > em {
    font-family: inherit;
    font-style: normal;
    color: $text-color-light; } }

.label-float {
  position: relative;
  float: left;
  $space-outer-horizontal: $form-border-width + $form-input-space-h - nth($form-label-space-inner, 2);
  $space-outer-vertical: round-float($form-label-line-height / 2, 1) + 0.1;
  margin: 0 #{$space-outer-horizontal} -#{$space-outer-vertical};
  padding: $form-label-space-inner;
  @include font-size($form-label-font-size, $form-label-line-height);
  color: $text-color;
  background-color: white;

  @include media($form-media--net) {
    $space-outer-horizontal: $form-border-width--net + $form-input-space-h--net - nth($form-label-space-inner--net, 2);
    $space-outer-vertical: round-float($form-label-line-height--net / 2, 1) + 0.1;
    margin: 0 #{$space-outer-horizontal} -#{$space-outer-vertical};
    padding: $form-label-space-inner--net;
    @include font-size($form-label-font-size--net, $form-label-line-height--net); }

  @include media($form-media--mob) {
    $space-outer-horizontal: $form-border-width--mob + $form-input-space-h--mob - nth($form-label-space-inner--mob, 2);
    $space-outer-vertical: round-float($form-label-line-height--mob / 2, 1) + 0.1;
    margin: 0 #{$space-outer-horizontal} -#{$space-outer-vertical};
    padding: $form-label-space-inner--mob;
    @include font-size($form-label-font-size--mob, $form-label-line-height--mob); } }

// .form-def, .form-def_dark:
// input[type="text"]
// input[type="password"]
// input[type="email"]
// input[type="search"]
// textarea

.form-def, .form-def_dark {
  display: block;
  @include inputs-common-styles;
  @include inputs-font-styles;
  @include forms-space;
  // transition
  $prop: color, border-color;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  @include media($form-media--net) {
    @include inputs-common-styles--net;
    @include inputs-font-style--net;
    @include forms-space--net; }

  @include media($form-media--mob) {
    @include inputs-common-styles--mob;
    @include inputs-font-style--mob;
    @include forms-space--mob; }

  // Textarea
  @at-root {
    textarea {
      &.form-def, &.form-def_dark {
        $lines: 1;
        $height: round-float($form-line-height * $lines, 1) + $form-input-space-vt + $form-input-space-vb + round-float($form-border-width * 2, 1);
        min-height: $height;
        height: $height;
        resize: none;

        @include media($form-media--net) {
          $lines: 1;
          $height: round-float($form-line-height--net * $lines, 1) + $form-input-space-vt--net + $form-input-space-vb--net + round-float($form-border-width--net * 2, 1);
          min-height: $height;
          height: $height; }

        @include media($form-media--mob) {
          $lines: 1;
          $height: round-float($form-line-height--mob * $lines, 1) + $form-input-space-vt--mob + $form-input-space-vb--mob + round-float($form-border-width--mob * 2, 1);
          min-height: $height;
          height: $height; } } } }

  // Последний элемент в списке
  &:last-child {
    margin-bottom: 0; }

  // Placeholder
  @include placeholder {
    @include font-normal;
    color: $form-placeholder-color;
    // transition
    $prop: opacity, color;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed; } }

  // Placeholder hide on focus
  @include placeholder-focus {
    opacity: 0; }

  // Focus
  &:focus {
    cursor: text;
    border-color: $form-border-color--focus; }

  // Hover
  &:hover {
    border-color: $form-border-color--focus; }

  // Focus (js classes)
  &.focused {
    @include focused; }

  // Has value (js classes)
  // Label
  @at-root {
    label {
      &.filled {
        color: $text-color-light; } } }

  // Success
  @at-root {
    label {
      &.success {
        @include form-success; } } }

  &.success {
    @include form-success;

    // Placeholder
    @include placeholder {
      color: $form-success-color; } }

  // Error
  @at-root label {
    &.error {
      @include form-error; } }

  &.error {
    @include form-error;

    // Placeholder
    @include placeholder {
      color: $form-error-color; } }

  // Disabled
  &[disabled] {
    cursor: default !important;
    color: $form-text-color--disabled !important;
    background-color: $form-bg-color--disabled !important;
    border-color: $form-border-color--disabled !important;
    box-shadow: none !important;

    @include placeholder {
      color: $form-text-color--disabled !important; } } }

// dark
.label_dark {
  color: $base-color-alt;

  &.filled {
    color: rgba($base-color-alt, 0.5); }

  &.success {
    @include form-success; }

  &.error {
    color: white; } }

.form-def_dark {
  border-color: rgba($base-color-alt, 0.3);

  @include placeholder {
    color: rgba($base-color-alt, 0.3); }

  &:focus, &:hover, &.focused {
    border-color: rgba($base-color-alt, 0.8); }

  &.success {
    @include form-success;

    @include placeholder {
      color: $form-success-color; } }

  &.error {
    color: white;
    border-color: white;

    @include placeholder {
      color: white; } }

  & ~ .error-feedback {
    color: white; } }

// form footer
.form__footer {
  padding-top: 0.8rem;
  text-align: right;

  @include media('<tab-s') {
    padding-top: 0; } }

@import "unit";
@import "group";
@import "radiocheck";
@import "switcher";
@import "select";
@import "validation";

/* form elements end */
